        <p>Center is at {{ currentCenter }} and the zoom is: {{ currentZoom }}</p>
      </div>
      <button @click="showMap = !showMap">
          Toggle map
        </button> -->

      <!-- Maps -->
      <l-map v-if="showMap" :zoom="zoom" :center="center" :options="mapOptions" 
        @update:center="centerUpdate" @update:zoom="zoomUpdate" @click="getLatLong">
        
        <l-tile-layer :url="url" :attribution="attribution"/>

        <!-- Marker -->
        <l-marker :lat-lng="[marker.lat,marker.long]" v-for="(marker, indextr) in markers" 
          :key="indextr" @click="previewTraffic = true">
          <l-icon :icon-url="'/marker/'+marker.icon" />
            
