    //       photo:'https://images.unsplash.com/photo-1550989460-0adf9ea622e2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60',
    //       desc : 'Ullamco nisi veniam ex in velit pariatur nulla. Nisi adipisicing nulla cupidatat non ea. Cillum irure adipisicing enim dolore commodo ut dolor.'
    //     }
    // ];
    // console.log(self.markers)
    // self.showMap = true;
    // console.log(self.showMap)
    //             }, 2000);
    
